import { Heading, ToggleButton } from '@/checkout/components';
import { PaymentMethodsSkeleton } from '@/checkout/components/PaymentMethods';
import '@/checkout/components/PaymentMethods/PaymentMethods.css';
import { PaymentMethod } from '@/checkout/data/models/payment-method';
import {
  Divider,
  Icon,
  IllustratedMessage,
  ProgressSpinner,
} from '@adobe/elsie/components';
import { useText } from '@adobe/elsie/i18n';
import { Wallet } from '@adobe/elsie/icons';
import { classes } from '@adobe/elsie/lib';
import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes } from 'preact/compat';

export interface PaymentMethodsProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'onChange'> {
  paymentMethodContent?: VNode;
  isLoading?: boolean;
  onChange?: (value: string) => void;
  options?: PaymentMethod[];
  selection?: string;
}

interface PaymentOptionProps {
  code: string;
  isLoading?: boolean;
  isSelected: boolean;
  onChange: (value: string) => void;
  title: string;
}

const PaymentOption: FunctionComponent<PaymentOptionProps> = ({
  code,
  isLoading,
  isSelected,
  onChange,
  title,
}) => {
  return (
    <ToggleButton
      className="checkout-payment-methods__method"
      label={title}
      name="payment-method"
      value={code}
      selected={isSelected}
      onChange={onChange}
      radioButtonProps={{
        'aria-busy': isLoading,
      }}
      // TODO: Add icon
    />
  );
};

export const PaymentMethods: FunctionComponent<PaymentMethodsProps> = ({
  className,
  paymentMethodContent,
  isLoading,
  onChange = () => {},
  options,
  selection,
}) => {
  const dictionary = useText({
    Title: 'Checkout.PaymentMethods.title',
    EmptyState: 'Checkout.PaymentMethods.emptyState',
  });

  if (options === undefined) return <PaymentMethodsSkeleton />;

  return (
    <div className={classes(['checkout-payment-methods', className])}>
      <Heading level={2} className="checkout-payment-methods__title">
        {dictionary.Title}
      </Heading>

      {!isLoading && options!.length === 0 && (
        <IllustratedMessage
          icon={<Icon source={Wallet} />}
          message={<p>{dictionary.EmptyState}</p>}
        />
      )}

      <div className={classes(['checkout-payment-methods__wrapper'])}>
        {isLoading && (
          <ProgressSpinner className="checkout-payment-methods__spinner" />
        )}

        <div
          className={classes([
            'checkout-payment-methods__methods',
            ['checkout-payment-methods--loading', isLoading],
            ['checkout-payment-methods--full-width', options!.length % 2 !== 0],
          ])}
        >
          {options?.map((method) => (
            <PaymentOption
              code={method.code}
              key={method.code}
              onChange={onChange}
              isSelected={method.code === selection}
              title={method.title}
            />
          ))}
        </div>

        {paymentMethodContent && (
          <div className="checkout-payment-methods__content">
            {paymentMethodContent}
          </div>
        )}
      </div>
      <Divider variant="primary" />
    </div>
  );
};
