import { setPaymentMethod as setPaymentMethodOnCart } from '@/checkout/api';
import { PaymentMethods as PaymentMethodsComponent } from '@/checkout/components/PaymentMethods/PaymentMethods';
import {
  PaymentMethodHandler,
  defaultHandlers,
} from '@/checkout/containers/PaymentMethods/defaultHandlers';
import { cartSignal } from '@/checkout/signals';
import { Container, Slot, SlotProps } from '@adobe/elsie/lib';
import {
  HTMLAttributes,
  useCallback,
  useEffect,
  useState,
} from 'preact/compat';

export interface PaymentMethodSlotContext {
  addPaymentMethodHandler: (
    code: string,
    handler: { render: SlotProps<PaymentMethodContentSlotContext> }
  ) => void;
  replaceHTML: (domElement: HTMLElement) => void;
}

export interface PaymentMethodContentSlotContext {
  cartId: string;
  onPlaceOrder: (fn: () => Promise<void>) => void;
  handleServerError: (error: any) => void;
  replaceHTML: (domElement: HTMLElement) => void;
}

export interface PaymentMethodsProps extends HTMLAttributes<HTMLDivElement> {
  paymentMethodsSlot?: SlotProps<PaymentMethodSlotContext>;
  onPlaceOrder: (fn: () => Promise<void>) => void;
  handleServerError: (error: any) => void;
  isShippingInfoRequired?: boolean;
}

function usePaymentMethods(props: PaymentMethodsProps) {
  const {
    onPlaceOrder,
    handleServerError,
    paymentMethodsSlot,
    isShippingInfoRequired,
  } = props;

  const [selectedPaymentMethod, setSelectedPaymentMethod] = useState<
    string | undefined
  >();
  const [temporarySelectedPaymentMethod, setTemporarySelectedPaymentMethod] =
    useState<string | undefined>();

  const [paymentMethodHandlers, setPaymentMethodHandlers] =
    useState(defaultHandlers);

  const [isDefaultContentUsed, setIsDefaultContentUsed] = useState(true);

  const { data: cartData } = cartSignal.value;
  const cartId = cartData?.id ?? '';
  const hasCartData = !!cartData;
  const hasAddress = !!cartData?.shippingAddresses?.[0];
  const availablePaymentMethods = cartData?.availablePaymentMethods;
  const cartSelectedPaymentMethod = cartData?.selectedPaymentMethod;

  const setAndSelectPaymentMethod = useCallback(
    async (paymentMethod: string) => {
      try {
        setSelectedPaymentMethod(paymentMethod);

        if (!hasAddress && isShippingInfoRequired) return;

        if (paymentMethod === cartSelectedPaymentMethod?.code) return;

        await setPaymentMethodOnCart({ cartId, paymentMethod });
      } catch (error: any) {
        console.error('setting payment method failed:', error);
      }
    },
    [cartId, hasAddress, isShippingInfoRequired, cartSelectedPaymentMethod]
  );

  useEffect(() => {
    if (!hasCartData) return;

    if (cartSelectedPaymentMethod?.code) {
      const selectedPaymentMethodIsAvailable = availablePaymentMethods?.some(
        (method: any) => method.code === cartSelectedPaymentMethod.code
      );

      if (selectedPaymentMethodIsAvailable) {
        // select the method from the backend
        setSelectedPaymentMethod(cartSelectedPaymentMethod.code);
      } else if (availablePaymentMethods?.length) {
        // select the first available method
        setAndSelectPaymentMethod(availablePaymentMethods[0]!.code);
      } else {
        setSelectedPaymentMethod(undefined);
      }
    } else {
      // default to the temporarily selected one or the first one
      availablePaymentMethods![0] &&
        setAndSelectPaymentMethod(
          temporarySelectedPaymentMethod || availablePaymentMethods![0].code
        );
    }
  }, [
    availablePaymentMethods,
    hasCartData,
    cartSelectedPaymentMethod,
    setAndSelectPaymentMethod,
    temporarySelectedPaymentMethod,
  ]);

  const onPaymentMethodChange = async (value: string) => {
    await setAndSelectPaymentMethod(value);
    if (!cartSelectedPaymentMethod?.code) {
      setTemporarySelectedPaymentMethod(value);
    }
  };

  const addPaymentMethodHandler = useCallback(
    (code: string, handler: PaymentMethodHandler) => {
      if (!code) {
        console.warn(
          'Payment method handler is ignored because it has no code'
        );
        return;
      }

      if (!handler) {
        console.warn('Payment method handler is ignored because it is empty');
        return;
      }

      setPaymentMethodHandlers((prevHandlers) => ({
        ...prevHandlers,
        [code]: handler,
      }));
    },
    []
  );

  // Run the main payment method container slot callback
  const mainSlotContent = paymentMethodsSlot ? (
    <Slot
      name="PaymentMethods"
      slot={paymentMethodsSlot}
      context={{
        addPaymentMethodHandler,
        replaceHTML(domElement) {
          this.replaceWith(domElement);
          setIsDefaultContentUsed(false);
        },
      }}
    />
  ) : null;

  const selectedPaymentMethodHandler = selectedPaymentMethod
    ? paymentMethodHandlers[selectedPaymentMethod]
    : null;

  const paymentMethodContent = selectedPaymentMethodHandler ? (
    <Slot
      key={selectedPaymentMethodHandler}
      name="PaymentMethodContent"
      slot={selectedPaymentMethodHandler.render}
      context={{
        cartId,
        onPlaceOrder,
        handleServerError,
        replaceHTML(domElement) {
          this.replaceWith(domElement);
        },
      }}
    />
  ) : undefined;

  useEffect(() => {
    // Content has been replaced but payment method handlers have been added
    if (!isDefaultContentUsed && paymentMethodHandlers != defaultHandlers) {
      console.warn(
        'Payment method handlers you have added are ignored because the default content has been replaced'
      );
    }
  }, [isDefaultContentUsed, paymentMethodHandlers]);

  return {
    availablePaymentMethods,
    selectedPaymentMethod,
    onPaymentMethodChange,
    paymentMethodContent,
    mainSlotContent,
    isDefaultContentUsed,
  };
}

export const PaymentMethods: Container<PaymentMethodsProps> = (props) => {
  const {
    onPlaceOrder,
    handleServerError,
    paymentMethodsSlot,
    isShippingInfoRequired = true,
  } = props;
  const {
    availablePaymentMethods,
    selectedPaymentMethod,
    onPaymentMethodChange,
    paymentMethodContent,
    mainSlotContent,
    isDefaultContentUsed,
  } = usePaymentMethods({
    onPlaceOrder,
    handleServerError,
    paymentMethodsSlot,
    isShippingInfoRequired,
  });

  return (
    <>
      {mainSlotContent && (
        // @ts-ignore
        <mainSlotContent.type
          ref={mainSlotContent.ref}
          {...mainSlotContent.props}
        />
      )}
      {isDefaultContentUsed && (
        <PaymentMethodsComponent
          options={availablePaymentMethods}
          selection={selectedPaymentMethod}
          onChange={onPaymentMethodChange}
          paymentMethodContent={paymentMethodContent}
          isLoading={cartSignal.value.pending}
        />
      )}
    </>
  );
};
