import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/checkout/components/ToggleButton/ToggleButton.css';
import { RadioButton } from '@adobe/elsie/components';

export interface ToggleButtonProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'onChange' | 'icon'> {
  label: string;
  name: string;
  value: string;
  selected: boolean;
  onChange: (value: string) => void;
  icon?:
    | VNode<HTMLAttributes<SVGSVGElement>>
    | VNode<HTMLAttributes<HTMLImageElement>>;
  radioButtonProps?: Omit<
    HTMLAttributes<HTMLInputElement>,
    'size' | 'label' | 'name' | 'value' | 'checked' | 'disabled'
  >;
}

export const ToggleButton: FunctionComponent<ToggleButtonProps> = ({
  label,
  name,
  value,
  icon,
  selected,
  onChange,
  className,
  children,
  radioButtonProps,
  ...props
}) => {
  return (
    <div
      className={classes([
        'checkout-toggle-button',
        className,
        ['checkout-toggle-button__selected', selected],
      ])}
      {...props}
    >
      <label className="checkout-toggle-button__actionButton">
        <RadioButton
          {...radioButtonProps}
          label=""
          name={name}
          value={value}
          checked={selected}
          onChange={() => onChange(value)}
          aria-label={label}
          className={classes([
            className,
            'checkout-toggle-button__radioButton',
          ])}
        />

        <div className="checkout-toggle-button__content">
          {icon && (
            <icon.type
              {...icon?.props}
              className="checkout-toggle-button__icon"
            />
          )}

          {label}
        </div>
      </label>
    </div>
  );
};
